.success {
  @include cover;
  background-image: url('../img/success/success-bg.png');
  padding: 105px 0;
  min-height: 1000px;
  background-color: #EBF2F9;
  background-attachment: fixed;
  @include r(767) {
    padding: 70px 0 540px; }
  @include r(992) {
    background-size: auto 900px;
    background-position: 60% 100%; }
  h2 {
    font-size: 64px;
    margin-bottom: 30px;
    color: #050606;
    span {
      color: #53BF51; }
    @include r(767) {
      font-size: 36px; } }
  h3 {
    font-weight: bold;
    font-size: 24px;
    color: #050606;
    margin-bottom: 60px;
    @include r(767) {
      font-size: 16px;
      margin-bottom: 40px; } }
  p {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 70px;
    max-width: 440px;
    width: 100%;
    position: relative;
    left: 100%;
    transform: translate(-100%);
    @include r(992) {
      margin-top: 60px;
      transform: translate(-50%);
      left: 50%; } }
  &__more {
    background: #FFFFFF;
    border-radius: 40px;
    padding: 15px 60px;
    color: #53BF51;
    font-weight: bold;
    font-size: 16px;
    display: inline-block;
    @include relative-x;
    margin-top: 15px;
    &:hover {
      background: #53BF51;
      color: #fff; }
    i {
      font-size: 22px;
      margin-left: 13px;
      position: relative;
      top: 2px; } }
  &__form {
    max-width: 440px;
    width: 100%;
    position: relative;
    left: 100%;
    transform: translate(-100%);
    border-radius: 16px;
    overflow: hidden;
    @include r(992) {
      transform: translate(-50%);
      left: 50%; }
    h4 {
      padding: 35px 10px;
      font-weight: bold;
      text-align: center;
      font-size: 24px;
      line-height: 1;
      background: #53BF51;
      border-radius: 16px 16px 0px 0px;
      @include r(767) {
        padding: 25px 10px; }
      span {
        color: #fff; } }
    &__content {
      padding: 50px 50px 30px;
      backdrop-filter: blur(8px);
      background: rgba(255, 255, 255, 0.9);
      display: block;
      @include r(767) {
        padding: 25px 20px; }
      label {
        margin-bottom: 30px !important; }
      input {
        padding: 15px 20px;
        background: #F6F7F9;
        margin-bottom: 30px; }
      button {
        width: 265px;
        @include relative-x;
        @include r(500) {
          width: 100%; } }
      a {
        font-size: 16px;
        font-weight: bold;
        padding: 10px 20px;
        color: #8D8D8C;
        display: block;
        text-align: center;
        margin-top: 15px;
        &:hover {
          text-decoration: underline; } } } } }
