.system {
  padding-top: 115px;
  @include r(767) {
    padding-top: 60px; }
  h2 {
    font-weight: bold;
    font-size: 64px;
    color: #050606;
    span {
      color: #53BF51; }
    @include r(767) {
      font-size: 36px; } }
  &__block {
    height: 100%;
    @include center-y;
    padding-bottom: 90px;
    @include r(767) {
      padding-bottom: 60px; }
    b {
      font-size: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #000000;
      font-weight: bold;
      width: 65px;
      height: 65px;
      border-radius: 50%;
      position: relative;
      box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
      background: #fff;
      margin-right: 30px;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        z-index: 2;
        transform: rotate(90deg); }
      &::after {
        content: '';
        position: absolute;
        width: calc(100% - 8px);
        height: calc(100% - 8px);
        background: #fff;
        display: block;
        z-index: 3;
        border-radius: 50%; }
      &.circle {
        &-1::before {
          background-image: linear-gradient(0deg, #fff 50%, transparent 50%), linear-gradient(-108deg, #fff 50%, #53BF51 50%); }
        &-2::before {
          background-image: linear-gradient(0deg, #fff 50%, transparent 50%), linear-gradient(-36deg, #fff 50%, #53BF51 50%); }
        &-3::before {
          background-image: linear-gradient(0deg, transparent 50%, #53BF51 50%), linear-gradient(36deg, #fff 50%, #53BF51 50%); }
        &-4::before {
          background-image: linear-gradient(0deg, transparent 50%, #53BF51 50%), linear-gradient(108deg, #fff 50%, #53BF51 50%); }
        &-5::before {
          background-image: linear-gradient(0deg, transparent 50%, #53BF51 50%), linear-gradient(180deg, #fff 50%, #53BF51 50%); } }
      span {
        z-index: 4; } }
    div {
      flex: 1; }
    h4 {
      font-weight: bold;
      font-size: 16px;
      margin-bottom: 10px; }
    p {
      color: #8D8D8C;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.5; } } }
